import React from 'react'
import { Link } from 'gatsby'
import Img from 'gatsby-image'

import modalStyles from './modal.module.css'

const Modal = ({ node, image, handleClose }) => {
  const [isVertical, setIsVertical] = React.useState(false)

  const containerRef = React.useRef(null)

  React.useEffect(() => {
    if (!containerRef.current) return

    const getLayoutDimensions = () => {
      const rect = containerRef.current.getBoundingClientRect()
      if (rect.height > rect.width) {
        setIsVertical(true)
      } else {
        setIsVertical(false)
      }
    }

    getLayoutDimensions()

    window.addEventListener('resize', getLayoutDimensions)
    return () => window.removeEventListener('resize', getLayoutDimensions)
  }, [containerRef, setIsVertical])

  console.log(node)
  return (
    <div ref={containerRef} className={modalStyles.modal} style={isVertical ? { height: '80%' } : {}}>
      <h1 className={modalStyles.title} style={isVertical ? { fontSize: '1.5rem' } : {}}>
        {node.frontmatter.title}
      </h1>
      <div className={modalStyles.container} style={{ flexDirection: `${isVertical ? 'column' : 'row'}` }}>
        <div className={modalStyles.image} style={isVertical ? { width: '100%', height: '50%' } : {}}>
          <Img
            className={modalStyles.img}
            fluid={image.childImageSharp.fluid}
            imgStyle={{
              objectFit: 'contain',
            }}
            alt="Album Art"
          />
        </div>
        <div className={modalStyles.content} style={isVertical ? { width: '100%', height: '50%' } : {}}>
          <p className={modalStyles.innerTitle}>{node.frontmatter.title}</p>
          <p className={modalStyles.date}>
            {node.frontmatter.releaseDate} - {node.frontmatter.albumType}
          </p>
          <ol>
            {node.frontmatter.tracks?.map((track, i) => (
              <li key={i}>{track}</li>
            ))}
          </ol>
          <div className={modalStyles.linkContainer}>
            <div>
              Available on{' '}
              <Link
                className={modalStyles.musicLink}
                to="https://open.spotify.com/artist/5tWnRXHI8g2I0adOEzNh7X?si=ikiZqJVGQDW_n1UVQvEUtA"
                target="_blank"
              >
                Spotify
              </Link>
            </div>
            <div className={modalStyles.contact}>
              Or find it on my{' '}
              <Link className={modalStyles.musicLink} to="https://soundcloud.com/garyanewsome" target="_blank">
                Soundcloud
              </Link>
            </div>
          </div>
        </div>
      </div>
      <div className={modalStyles.footer}>
        <button className={modalStyles.footerButton} onClick={() => handleClose()}>
          CLOSE
        </button>
      </div>
    </div>
  )
}

export default Modal
